import React, { Component } from 'react';
import AccountSetup from './AccountSetup';
import WindowsOffer from './WindowsOffer';
import Confirm from './Confirm';
import Success from './Success';

export class Form extends Component {
    state = {
        step: 1, 
        clientname: '',
        contactperson: '', 
        email: '',
        phone: '',
        shipaddress: '',
        totalvaluedoors: '',
        totalvaluewindow: '',
        productdoor: '',
        windowtype: '',
        profile: '',
        windowpackage: '',
        windowcolor: '',
        windowquantity: '',
        windowwidth: '',
        windowheight: ''
    };

    nextStep = () => {
        const { step } = this.state;
        this.setState({ step: step + 1 });
    };

    prevStep = () => {
        const { step } = this.state;
        this.setState({ step: step - 1 });
    };

    inputChange = input => e => {
        this.setState({
            [input]: e.target.value
        });
    };

    selectChange = select => e => {
        this.setState({
           [select]: e.target.values
        });
    };
  
    render() {
        const { step } = this.state;

        const { clientname, contactperson, email, phone, shipaddress, 
        totalvaluedoors, totalvaluewindow, quantitydoors, 
        quantitywindow, productdoor, windowtype, 
        profile, windowpackage, windowcolor, windowquantity, windowwidth, windowheight} = this.state;

        const values = { clientname, contactperson, email, phone, 
        shipaddress, totalvaluedoors, totalvaluewindow, 
        quantitydoors, quantitywindow, productdoor, windowtype, 
        profile, windowpackage, windowcolor, windowquantity, windowwidth, windowheight};

        switch (step) {
            case 1:
                return (
                    <AccountSetup
                        nextStep={this.nextStep}
                        inputChange={this.inputChange}
                        values={values}
                    />
                );
            case 2:
                return (
                    <WindowsOffer
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        inputChange={this.inputChange}
                        selectChange={this.selectChange}
                        values={values}
                    />
                );
            case 3:
                return (
                    <Confirm
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        values={values}
                    />
                );
            case 4:
                return (
                    <Success />
                );
        }
    }
}

export default Form